import axios from 'axios';


import { sessionService } from 'redux-react-session';

// Login
export const loginUser = (credentials, history, setFieldError,
    setSubmitting) =>
{
    // Make checks and get some data
    return () =>
    {


        axios.post("https://agile-atoll-50455.herokuapp.com/user/signin",
            credentials,
            {
                headers: {
                    "content-Type": "application/json"
                }
            }
        ).then((response) =>
        {
            const { data } = response;

            if (data.status === "FAILED")
            {
                const { message } = data;

                // check data
                if (message.include("credentials"))
                {
                    setFieldError("email", message);
                    setFieldError("password", message);
                }

                else if (message.include("password"))
                {
                    setFieldError("password", message)
                }

            } else if (data.status === "SUCCESS")
            {
                const userData = data.data[0];


                const token = userData._id;

                sessionService.saveSession(token).then(() =>
                {
                    sessionService.saveUser(userData).then(() =>
                    {
                        history.push("/dashboard");
                    }).catch(err => console.error(err))

                }).catch(err => console.error(err))
            }



            setSubmitting(false);

        }).catch(err => console.error(err))


    }
};



// Signup
export const signupUser = (credentials, history,
    setFieldError, setSubmitting) =>
{
    return (dispatch) =>
    {

        {

            axios.post("https://agile-atoll-50455.herokuapp.com/user/signup",
                credentials,
                {
                    headers: {
                        "content-Type": "application/json"
                    }
                }
            ).then((response) =>
            {
                const { data } = response;

                if (data.status === "FAILED")
                {
                    const { message } = data;

                    // Testing for different kinds of error
                    if (message.include("name"))
                    {
                        setFieldError("name", message);
                    } else if (message.includes("email"))
                    {
                        setFieldError("email", message)
                    } else if (message.includes("date"))
                    {
                        setFieldError("dateOfBirth", message)
                    } else if (message.includes("password"))
                    {
                        setFieldError("pasword", message)
                    }


                    // Complete submission

                    setSubmitting(false);

                } else if (data.status === "SUCCESS")
                {
                    const { email, password } = credentials;
                    //   Login user after signup

                    dispatch(loginUser({ email, password }, history, setFieldError, setSubmitting))

                }

            }).catch(err => console.error(err))


        }

    }
}

// Logout
export const logoutUser = (history) =>
{
    return () =>
    {
        sessionService.deleteSession();
        sessionService.deleteUser();
        history.push('/');
    }


};


